import "./Checkbox.scss";

/**
 * Checkbox 
 * @param {string} label -  
 * @param {string} name - name 
 * @param {string} value - value 
 * @param {boolean} checked -  
 * @param {function} onChange -  
 * @param {boolean} disabled -  
 * @param {string} className -  
 * @param {object} ...rest -  input props
 */
const Checkbox = ({
  label,
  name,
  value,
  checked = false,
  onChange,
  disabled = false,
  className = "",
  ...rest
}) => {
  return (
    <label className={`checkbox ${disabled ? "checkbox--disabled" : ""} ${className}`}>
      <input
        type="checkbox"
        name={name}
        value={value}
        checked={checked}
        onChange={onChange}
        disabled={disabled}
        className="checkbox__input"
        {...rest}
      />
      <span className="checkbox__box" aria-hidden="true">
        {checked && (
          <svg width="14" height="14" viewBox="0 0 14 14" fill="none">
            <path
              d="M11.6667 3.5L5.25 9.91667L2.33333 7"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
        )}
      </span>
      {label && <span className="checkbox__label">{label}</span>}
    </label>
  );
};

/**
 * CheckboxGroup 
 * @param {string} label -  
 * @param {Array} options -   [{ value, label, checked, disabled }]
 * @param {string} name - name 
 * @param {function} onChange -  
 * @param {string} className -  
 */
export const CheckboxGroup = ({ label, options = [], name, onChange, className = "" }) => {
  /**
   *     
   *   onChange  
   * @param {Event} e - input change  
   */
  const handleChange = (e) => {
    if (onChange) {
      //   checked    options  
      //  onChange     options  
      onChange(e, options.map((opt) => (opt.value === e.target.value ? { ...opt, checked: e.target.checked } : opt)));
    }
  };

  return (
    <div className={`checkbox-group ${className}`}>
      {label && <div className="checkbox-group__label">{label}</div>}
      <div className="checkbox-group__list">
        {options.map((option) => (
          <Checkbox
            key={option.value}
            name={name}
            value={option.value}
            label={option.label}
            checked={option.checked || false}
            disabled={option.disabled || false}
            onChange={handleChange}
          />
        ))}
      </div>
    </div>
  );
};

export default Checkbox;

