import "./Radio.scss";

/**
 * Radio 
 * @param {string} label -  
 * @param {string} name - name  ( )
 * @param {string} value - value 
 * @param {boolean} checked -  
 * @param {function} onChange -  
 * @param {boolean} disabled -  
 * @param {string} className -  
 * @param {object} ...rest -  input props
 */
const Radio = ({
  label,
  name,
  value,
  checked = false,
  onChange,
  disabled = false,
  className = "",
  ...rest
}) => {
  return (
    <label className={`radio ${disabled ? "radio--disabled" : ""} ${className}`}>
      <input
        type="radio"
        name={name}
        value={value}
        checked={checked}
        onChange={onChange}
        disabled={disabled}
        className="radio__input"
        {...rest}
      />
      <span className="radio__circle" aria-hidden="true">
        {checked && <span className="radio__dot" />}
      </span>
      {label && <span className="radio__label">{label}</span>}
    </label>
  );
};

/**
 * RadioGroup 
 * @param {string} label -  
 * @param {Array} options -   [{ value, label, disabled }]
 * @param {string} name - name 
 * @param {string} selectedValue -  
 * @param {function} onChange -  
 * @param {string} className -  
 */
export const RadioGroup = ({ label, options = [], name, selectedValue, onChange, className = "" }) => {
  /**
   *      
   *    onChange  
   * @param {Event} e - input change  
   */
  const handleChange = (e) => {
    if (onChange) {
      //  onChange      
      onChange(e, e.target.value);
    }
  };

  return (
    <div className={`radio-group ${className}`}>
      {label && <div className="radio-group__label">{label}</div>}
      <div className="radio-group__list" role="radiogroup" aria-label={label}>
        {options.map((option) => (
          <Radio
            key={option.value}
            name={name}
            value={option.value}
            label={option.label}
            checked={selectedValue === option.value}
            disabled={option.disabled || false}
            onChange={handleChange}
          />
        ))}
      </div>
    </div>
  );
};

export default Radio;

