const clone = (data) => JSON.parse(JSON.stringify(data));

const withLatency = (data, { delay = 800, shouldFail = false } = {}) =>
  new Promise((resolve, reject) => {
    setTimeout(() => {
      if (shouldFail) {
        reject(new Error("   "));
        return;
      }
      resolve(clone(data));
    }, delay);
  });

