import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import "./Image.scss";

const Image = ({
  src,
  alt = "",
  className = "",
  width,
  height,
  fallbackSrc,
  showFallback = true,
  onLoad,
  onError,
  ...props
}) => {
  const [imageStatus, setImageStatus] = useState("loading");
  const [aspectRatio, setAspectRatio] = useState(null);

  //  noimage  URL -   
  const noImageUrl = "data:image/svg+xml;base64,"; //   "No Image" 

  useEffect(() => {
    if (!src) {
      setImageStatus("error");
      return;
    }

    //     img  
    const img = document.createElement('img');
    img.onload = () => {
      setImageStatus("loaded");

      //  /    
      const ratio = img.naturalWidth / img.naturalHeight;
      if (ratio > 1.1) {
        setAspectRatio("landscape");
      } else if (ratio < 0.9) {
        setAspectRatio("portrait");
      } else {
        setAspectRatio("square");
      }

      onLoad?.(img);
    };

    img.onerror = () => {
      setImageStatus("error");
      onError?.();
    };

    img.src = src;
  }, [src, onLoad, onError]);

  const handleImageError = () => {
    setImageStatus("error");
    onError?.();
  };

  const handleImageLoad = () => {
    setImageStatus("loaded");
    onLoad?.();
  };

  //  
  const classes = [
    "image",
    imageStatus === "loading" && "image--loading",
    imageStatus === "error" && "image--error",
    aspectRatio && `image--${aspectRatio}`,
    className
  ].filter(Boolean).join(" ");

  if (imageStatus === "error" && !showFallback) {
    return null;
  }

  if (imageStatus === "error" && showFallback) {
    return (
      <div
        className={`${classes} image--fallback`}
        style={{ width, height }}
        role="img"
        aria-label={alt || "   "}
        {...props}
      >
        <div className="image__fallback-content">
          <div className="image__fallback-text">noimage</div>
        </div>
      </div>
    );
  }

  return (
    <>
      {imageStatus !== "error" && (
        <img
          src={src}
          alt={alt}
          className={classes}
          width={width}
          height={height}
          onLoad={handleImageLoad}
          onError={handleImageError}
          {...props}
        />
      )}
      {imageStatus === "error" && showFallback && (
        <img
          src={noImageUrl}
          alt={alt || "   "}
          className={`${classes} image--fallback`}
          width={width}
          height={height}
          {...props}
        />
      )}
    </>
  );
};

Image.propTypes = {
  src: PropTypes.string,
  alt: PropTypes.string,
  className: PropTypes.string,
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  fallbackSrc: PropTypes.string,
  showFallback: PropTypes.bool,
  onLoad: PropTypes.func,
  onError: PropTypes.func
};

export default Image;
