import "./Footer.scss";

const defaultNav = [
  { label: "", href: "#" },
  { label: "", href: "#" },
  { label: "FAQ", href: "#" },
  { label: "", href: "#" },
];

const defaultInfo = {
  address: "  123, ",
  contact: " 1234-5678 | support@starbucks.co.kr",
};

const defaultSns = ["Instagram", "Facebook", "Youtube"];

function Footer({ nav = defaultNav, info = defaultInfo, sns = defaultSns, logo = "" }) {
  return (
    <footer className="footer">
      <div className="footer__top">
        <div className="footer__logo">{logo}</div>
        <nav className="footer__nav" aria-label=" ">
          {nav.map((item) => (
            <a key={item.label} href={item.href}>
              {item.label}
            </a>
          ))}
        </nav>
      </div>
      <div className="footer__bottom">
        <div className="footer__info">
          <p>{info.address}</p>
          <p>{info.contact}</p>
        </div>
        <div className="footer__sns" aria-label=" ">
          {sns.map((name) => (
            <span key={name}>{name}</span>
          ))}
        </div>
      </div>
    </footer>
  );
}

export default Footer;

