import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import PageTemplate from "../../components/PageTemplate/PageTemplate";
import "./PublishingUrlPage.scss";
import { fetchMockUrls } from "../../mocks/mockData";

const PublishingUrlPage = () => {
  const [urls, setUrls] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const load = async () => {
      try {
        const data = await fetchMockUrls();
        setUrls(data);
      } catch (err) {
        console.error("URL   :", err);
        setError("  .");
      } finally {
        setIsLoading(false);
      }
    };

    load();
  }, []);

  return (
    <PageTemplate title=" URL ">
      <div className="publishing-url-page">
        <div className="publishing-url-page__table-container">
          <table className="publishing-url-table">
            <thead>
              <tr>
                <th>1</th>
                <th>2</th>
                <th>3</th>
                <th>4</th>
                <th>URL </th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {isLoading && (
                <tr>
                  <td colSpan={6}>  ...</td>
                </tr>
              )}
              {error && !isLoading && (
                <tr>
                  <td colSpan={6}>{error}</td>
                </tr>
              )}
              {!isLoading && !error && urls.map((item) => {
                const isLocalPage = item.url.startsWith('/');
                
                return (
                  <tr key={item.id}>
                    <td>{item.depth1 || "-"}</td>
                    <td>{item.depth2 || "-"}</td>
                    <td>{item.depth3 || "-"}</td>
                    <td>{item.depth4 || "-"}</td>
                    <td>
                      {isLocalPage ? (
                        <Link
                          to={item.url}
                          style={{ cursor: 'pointer', color: 'var(--color-accent)' }}
                        >
                          {item.url} 
                        </Link>
                      ) : (
                        <a href={item.url} target="_blank" rel="noopener noreferrer">
                          {item.url}
                        </a>
                      )}
                    </td>
                    <td>{item.description}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </PageTemplate>
  );
};

export default PublishingUrlPage;
