import { useState, useRef, useEffect } from "react";
import "./SearchField.scss";

/**
 * SearchField 
 * @param {string} placeholder -  
 * @param {string} value -   (controlled)
 * @param {function} onChange -   
 * @param {function} onSearch -    (Enter      )
 * @param {function} onClear -  
 * @param {boolean} showClearButton -    
 * @param {string} size - 'small' | 'medium' | 'large'
 * @param {string} variant - 'default' | 'filled' | 'outlined'
 * @param {boolean} disabled -  
 * @param {string} className -  
 */
const SearchField = ({
  placeholder = " ",
  value: controlledValue,
  onChange,
  onSearch,
  onClear,
  showClearButton = true,
  size = "medium",
  variant = "default",
  disabled = false,
  className = "",
}) => {
  const [internalValue, setInternalValue] = useState("");
  const inputRef = useRef(null);
  const isControlled = controlledValue !== undefined;

  const value = isControlled ? controlledValue : internalValue;

  const handleChange = (e) => {
    const newValue = e.target.value;
    if (!isControlled) {
      setInternalValue(newValue);
    }
    if (onChange) {
      onChange(e, newValue);
    }
  };

  const handleClear = () => {
    if (!isControlled) {
      setInternalValue("");
    }
    if (onChange) {
      onChange({ target: { value: "" } }, "");
    }
    if (onClear) {
      onClear();
    }
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };

  const handleSearch = () => {
    if (onSearch && value.trim()) {
      onSearch(value);
    }
  };

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleSearch();
    }
  };

  const hasValue = value && value.length > 0;

  return (
    <div
      className={`search-field search-field--${size} search-field--${variant} ${className} ${
        disabled ? "search-field--disabled" : ""
      }`}
    >
      <div className="search-field__wrapper">
        <div className="search-field__icon search-field__icon--search">
          <svg
            width="20"
            height="20"
            viewBox="0 0 20 20"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M9.16667 15.8333C12.8486 15.8333 15.8333 12.8486 15.8333 9.16667C15.8333 5.48477 12.8486 2.5 9.16667 2.5C5.48477 2.5 2.5 5.48477 2.5 9.16667C2.5 12.8486 5.48477 15.8333 9.16667 15.8333Z"
              stroke="currentColor"
              strokeWidth="1.5"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
            <path
              d="M17.5 17.5L13.875 13.875"
              stroke="currentColor"
              strokeWidth="1.5"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
        </div>

        <input
          ref={inputRef}
          type="text"
          className="search-field__input"
          placeholder={placeholder}
          value={value || ""}
          onChange={handleChange}
          onKeyDown={handleKeyDown}
          disabled={disabled}
          aria-label=" "
        />

        {showClearButton && hasValue && !disabled && (
          <button
            type="button"
            className="search-field__icon search-field__icon--clear"
            onClick={handleClear}
            aria-label=" "
          >
            <svg
              width="18"
              height="18"
              viewBox="0 0 18 18"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M13.5 4.5L4.5 13.5M4.5 4.5L13.5 13.5"
                stroke="currentColor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </button>
        )}

        {onSearch && (
          <button
            type="button"
            className="search-field__button"
            onClick={handleSearch}
            disabled={disabled || !hasValue}
            aria-label=""
          >
            
          </button>
        )}
      </div>
    </div>
  );
};

export default SearchField;

