import { useState, useRef, useEffect } from "react";
import "./Accordion.scss";

/**
 * Accordion 
 * @param {Array} items -    [{ id, label, content }]
 * @param {string} type - 'exclusive' ( )  'independent' ( )
 * @param {boolean} defaultOpenFirst -      
 * @param {string} className -  
 */
const Accordion = ({ 
  items = [], 
  type = "exclusive", // 'exclusive' | 'independent'
  defaultOpenFirst = false,
  className = "" 
}) => {
  const [openItems, setOpenItems] = useState(() => {
    if (defaultOpenFirst && items.length > 0) {
      return type === "exclusive" ? [items[0].id] : [items[0].id];
    }
    return [];
  });

  //      DOM  ( )
  const contentRefs = useRef({});

  /**
   *     
   *     
   * @param {string|number} itemId -   ID
   */
  const handleItemClick = (itemId) => {
    if (type === "exclusive") {
      // Exclusive :   ( )
      //   ,  
      setOpenItems((prev) => (prev.includes(itemId) ? [] : [itemId]));
    } else {
      // Independent :    (     )
      setOpenItems((prev) => {
        if (prev.includes(itemId)) {
          //    ( )
          return prev.filter((id) => id !== itemId);
        } else {
          //   ( )
          return [...prev, itemId];
        }
      });
    }
  };

  /**
   *    max-height  ()
   * openItems items   
   */
  useEffect(() => {
    items.forEach((item) => {
      const ref = contentRefs.current[item.id];
      if (ref) {
        if (openItems.includes(item.id)) {
          //     max-height 
          ref.style.maxHeight = ref.scrollHeight + "px";
        } else {
          //  max-height 0  (CSS transition )
          ref.style.maxHeight = "0";
        }
      }
    });
  }, [openItems, items]);

  /**
   *     
   *         
   * @param {Event} e -   
   */
  const handleContentClick = (e) => {
    //    (  handleItemClick  )
    e.stopPropagation();
  };

  return (
    <div className={`accordion ${className}`}>
      <ul className="accordion__list">
        {items.map((item) => {
          const isOpen = openItems.includes(item.id);
          return (
            <li
              key={item.id}
              className={`accordion__item ${isOpen ? "accordion__item--open" : ""}`}
              onClick={() => handleItemClick(item.id)}
            >
              <div className="accordion__header">
                <span className="accordion__label">{item.label}</span>
                <div className="accordion__icon">
                  <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M3 5.90446L3.8875 5L8 9.19108L12.1125 5L13 5.90446L8 11L3 5.90446Z"
                      fill="currentColor"
                    />
                  </svg>
                </div>
              </div>
              <div
                ref={(el) => (contentRefs.current[item.id] = el)}
                className="accordion__content-wrapper"
                onClick={handleContentClick}
              >
                <div className="accordion__content">
                  {typeof item.content === "string" ? (
                    <p>{item.content}</p>
                  ) : (
                    item.content
                  )}
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default Accordion;

