import { useEffect, useState, useRef } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { FreeMode } from "swiper/modules";
import "swiper/css";
import "swiper/css/free-mode";
import "./Tabs.scss";

const defaultTabItems = [
  { id: "detail", label: "", description: " , ,    ." },
  { id: "review", label: "", description: "   / ." },
  { id: "qa", label: "Q&A", description: "         ." },
];

/**
 * Tabs 
 * @param {Array} items -    [{ id, label, description, contentId? }]
 * @param {string} type - 'default' | 'scroll' | 'swiper' (: 'default')
 * @param {string} scrollContainerId -   ID (type 'scroll'  )
 * @param {function} onChange -    (activeTabId) => void
 * @param {string} className -  
 */
function Tabs({
  items = defaultTabItems,
  type = "default",
  scrollContainerId,
  onChange,
  className = "",
}) {
  //    ID 
  const [activeTab, setActiveTab] = useState(items[0]?.id);
  // Swiper   (swiper   )
  const swiperRef = useRef(null);
  //    DOM  (scroll     )
  const tabRefs = useRef({});
  //    (scroll   )
  const scrollContainerRef = useRef(null);

  /**
   * items      
   */
  useEffect(() => {
    //       
    setActiveTab(items[0]?.id);
  }, [items]);

  /**
   * scroll      
   */
  useEffect(() => {
    // scrollContainerId    
    if (type === "scroll" && scrollContainerId) {
      scrollContainerRef.current = document.getElementById(scrollContainerId);
    }
  }, [type, scrollContainerId]);

  if (!items || items.length === 0) {
    return <div className="tabs-demo__empty">  .</div>;
  }

  /**
   *    
   *     
   * @param {string} itemId -   ID
   * @param {number} index -    (swiper  )
   */
  const handleTabClick = (itemId, index) => {
    //    active    
    setActiveTab(itemId);
    //  onChange  
    onChange?.(itemId);

    if (type === "scroll") {
      // Scroll :      
      const targetElement = tabRefs.current[itemId];
      if (targetElement && scrollContainerId) {
        const container = document.getElementById(scrollContainerId);
        if (container) {
          //      
          const containerRect = container.getBoundingClientRect();
          const targetRect = targetElement.getBoundingClientRect();
          //      
          // (       )
          const scrollLeft = container.scrollLeft + targetRect.left - containerRect.left - (containerRect.width / 2) + (targetRect.width / 2);
          
          //    
          container.scrollTo({
            left: scrollLeft,
            behavior: "smooth",
          });
        }
      }
    } else if (type === "swiper" && swiperRef.current) {
      // Swiper : Swiper   
      // slideTo    active   
      // 300ms    
      swiperRef.current.slideTo(index, 300);
    }
  };

  const activeItem = items.find((item) => item.id === activeTab);

  // Swiper 
  if (type === "swiper") {
    return (
      <div className={`tabs tabs--swiper ${className}`}>
        <div className="tabs__wrapper">
          <Swiper
            modules={[FreeMode]}
            freeMode={{
              enabled: true,
              sticky: false,
            }}
            slidesPerView="auto"
            spaceBetween={8}
            centeredSlides={true}
            centeredSlidesBounds={true}
            onSwiper={(swiper) => {
              swiperRef.current = swiper;
            }}
            onSlideChange={(swiper) => {
              // Swiper    activeTab  (   )
              // realIndex      (loop   )
              const realIndex = swiper.realIndex;
              //    activeTab  
              if (items[realIndex] && activeTab !== items[realIndex].id) {
                setActiveTab(items[realIndex].id);
                onChange?.(items[realIndex].id);
              }
            }}
            className="tabs__swiper"
          >
            {items.map((item, index) => (
              <SwiperSlide key={item.id} className="tabs__slide">
                <button
                  type="button"
                  role="tab"
                  aria-selected={activeTab === item.id}
                  onClick={() => handleTabClick(item.id, index)}
                  className={`tabs__button ${activeTab === item.id ? "is-active" : ""}`}
                >
                  {item.label}
                </button>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
        {activeItem && (
          <div
            className="tabs__tabpanel"
            role="tabpanel"
            aria-live="polite"
            aria-label={`${activeItem.label}  `}
          >
            {activeItem.description ?? " ."}
          </div>
        )}
      </div>
    );
  }

  // Scroll 
  if (type === "scroll") {
    return (
      <div className={`tabs tabs--scroll ${className}`}>
        <div className="tabs__scroll-container" id={scrollContainerId || undefined}>
          <div className="tabs__tablist" role="tablist" aria-label="  ">
            {items.map((item, index) => (
              <button
                key={item.id}
                ref={(el) => (tabRefs.current[item.id] = el)}
                type="button"
                role="tab"
                aria-selected={activeTab === item.id}
                onClick={() => handleTabClick(item.id, index)}
                className={`tabs__button ${activeTab === item.id ? "is-active" : ""}`}
              >
                {item.label}
              </button>
            ))}
          </div>
        </div>
        {activeItem && (
          <div
            className="tabs__tabpanel"
            role="tabpanel"
            aria-live="polite"
            aria-label={`${activeItem.label}  `}
          >
            {activeItem.description ?? " ."}
          </div>
        )}
      </div>
    );
  }

  // Default  ( )
  return (
    <div className={`tabs tabs--default ${className}`}>
      <div className="tabs__tablist" role="tablist" aria-label="  ">
        {items.map((item) => (
          <button
            key={item.id}
            type="button"
            role="tab"
            aria-selected={activeTab === item.id}
            onClick={() => handleTabClick(item.id)}
            className={`tabs__button ${activeTab === item.id ? "is-active" : ""}`}
          >
            {item.label}
          </button>
        ))}
      </div>
      {activeItem && (
        <div
          className="tabs__tabpanel"
          role="tabpanel"
          aria-live="polite"
          aria-label={`${activeItem.label}  `}
        >
          {activeItem.description ?? " ."}
        </div>
      )}
    </div>
  );
}

export default Tabs;

