import Image from "../Image/Image";
import Badge from "../Badge/Badge";
import "./Card.scss";

/**
 * Card 
 * @param {string} variant - 'product' | 'content' (: 'content')
 * @param {string} image -  URL
 * @param {string} imageAlt -  alt 
 * @param {string} title -  
 * @param {string} description -  
 * @param {string} price -  ( )
 * @param {string} badge -  
 * @param {string} badgeVariant -  variant
 * @param {ReactNode} children -  
 * @param {function} onClick -  
 * @param {boolean} hoverable - hover   (: true)
 * @param {string} className -  
 */
const Card = ({
  variant = "content",
  image,
  imageAlt,
  title,
  description,
  price,
  badge,
  badgeVariant = "default",
  children,
  onClick,
  hoverable = true,
  className = "",
}) => {
  const isProduct = variant === "product";
  const isClickable = !!onClick;

  return (
    <div
      className={`card card--${variant} ${hoverable ? "card--hoverable" : ""} ${
        isClickable ? "card--clickable" : ""
      } ${className}`}
      onClick={onClick}
      role={isClickable ? "button" : undefined}
      tabIndex={isClickable ? 0 : undefined}
      onKeyDown={(e) => {
        if (isClickable && (e.key === "Enter" || e.key === " ")) {
          e.preventDefault();
          onClick?.(e);
        }
      }}
    >
      {image && (
        <div className="card__image">
          <Image src={image} alt={imageAlt || title || ""} className="card__image-element" />
          {badge && (
            <div className="card__badge">
              <Badge variant={badgeVariant} size="small">
                {badge}
              </Badge>
            </div>
          )}
        </div>
      )}

      <div className="card__content">
        {title && <h4 className="card__title">{title}</h4>}
        {description && <p className="card__description">{description}</p>}
        {price && <div className="card__price">{price}</div>}
        {children}
      </div>
    </div>
  );
};

export default Card;

