import { useState } from "react";
import "./ListSync.scss";

const defaultOptions = [
  { value: "", label: "" },
  { value: "", label: "" },
  { value: "", label: "" },
  { value: "", label: "" },
  { value: "", label: "" },
];

function ListSync({ options = defaultOptions, onChange }) {
  const [items, setItems] = useState([]);

  const handleAdd = (option) => {
    //  : value     
    if (items.some((item) => item.value === option.value)) return;
    const next = [...items, option];
    setItems(next);
    onChange?.(next);
  };

  const handleRemove = (idx) => {
    const next = items.filter((_, i) => i !== idx);
    setItems(next);
    onChange?.(next);
  };

  return (
    <div className="list-sync">
      <div className="list-sync__source" aria-label="  ">
        {options.map((option) => (
          <button
            key={option.value}
            type="button"
            className="list-sync__option"
            onClick={() => handleAdd(option)}
          >
            {option.label}
          </button>
        ))}
      </div>

      <div className="list-sync__target">
        <div className="list-sync__target-head">
          <span> </span>
          <span className="list-sync__count">{items.length}</span>
        </div>
        {items.length === 0 ? (
          <p className="list-sync__empty">   .</p>
        ) : (
          <ul className="list-sync__list">
            {items.map((item, idx) => (
              <li key={`${item.value}-${idx}`}>
                <span>{item.label}</span>
                <button
                  type="button"
                  className="list-sync__remove"
                  onClick={() => handleRemove(idx)}
                  aria-label={`${item.label} `}
                >
                  
                </button>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
}

export default ListSync;

