import "./List.scss";

/**
 * ListItem 
 * @param {ReactNode} children -   
 * @param {string} icon -  (  )
 * @param {string} prefix -   
 * @param {string} suffix -   
 * @param {function} onClick -  
 * @param {boolean} disabled -  
 * @param {string} className -  
 */
export const ListItem = ({
  children,
  icon,
  prefix,
  suffix,
  onClick,
  disabled = false,
  className = "",
}) => {
  const isClickable = !!onClick && !disabled;

  const content = (
    <>
      {icon && <span className="list-item__icon" aria-hidden="true">{icon}</span>}
      {prefix && <span className="list-item__prefix">{prefix}</span>}
      <span className="list-item__content">{children}</span>
      {suffix && <span className="list-item__suffix">{suffix}</span>}
    </>
  );

  if (isClickable) {
    return (
      <li
        className={`list-item list-item--clickable ${disabled ? "list-item--disabled" : ""} ${className}`}
        onClick={onClick}
        role="button"
        tabIndex={disabled ? -1 : 0}
        onKeyDown={(e) => {
          if (!disabled && (e.key === "Enter" || e.key === " ")) {
            e.preventDefault();
            onClick?.(e);
          }
        }}
        aria-disabled={disabled}
      >
        {content}
      </li>
    );
  }

  return (
    <li className={`list-item ${disabled ? "list-item--disabled" : ""} ${className}`}>
      {content}
    </li>
  );
};

/**
 * List 
 * @param {Array} items -    [{ id, content, icon, prefix, suffix, onClick, disabled }]
 * @param {string} variant - 'text' | 'icon' (: 'text')
 * @param {boolean} bordered -    (: true)
 * @param {boolean} divided -    (: false)
 * @param {string} className -  
 * @param {ReactNode} children -  ListItem children  
 */
const List = ({
  items = [],
  variant = "text",
  bordered = true,
  divided = false,
  className = "",
  children,
}) => {
  return (
    <ul
      className={`list list--${variant} ${bordered ? "list--bordered" : ""} ${
        divided ? "list--divided" : ""
      } ${className}`}
    >
      {children ||
        items.map((item) => (
          <ListItem
            key={item.id || item.key}
            icon={item.icon}
            prefix={item.prefix}
            suffix={item.suffix}
            onClick={item.onClick}
            disabled={item.disabled}
          >
            {item.content || item.children || item.label}
          </ListItem>
        ))}
    </ul>
  );
};

export default List;

