import { useState } from "react";
import "./FormSample.scss";

function FormSample() {
  const [form, setForm] = useState({ name: "", phone: "", address: "", email: "", password: "" });
  const [errors, setErrors] = useState({});
  const [message, setMessage] = useState("");

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
    setMessage("");
  };

  const validate = () => {
    const nextErrors = {};
    if (!form.name.trim()) nextErrors.name = " .";
    if (!/^01[0-9]-?\d{3,4}-?\d{4}$/.test(form.phone)) nextErrors.phone = "  010-1234-5678  .";
    if (!form.address.trim()) nextErrors.address = " .";
    if (!/\S+@\S+\.\S+/.test(form.email)) nextErrors.email = "  .";
    if (form.password.length < 8) nextErrors.password = " 8  .";
    return nextErrors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const nextErrors = validate();
    setErrors(nextErrors);
    if (Object.keys(nextErrors).length === 0) {
      setMessage("  !");
    }
  };

  return (
    <form className="form-sample" onSubmit={handleSubmit}>
      <label className="field">
        <span className="field__label"></span>
        <input
          type="text"
          name="name"
          placeholder=""
          value={form.name}
          onChange={handleChange}
          aria-invalid={!!errors.name}
        />
        <small className={`field__help ${errors.name ? "is-error" : ""}`}>
          {errors.name || "    ."}
        </small>
      </label>

      <label className="field">
        <span className="field__label"> </span>
        <input
          type="tel"
          name="phone"
          placeholder="010-1234-5678"
          value={form.phone}
          onChange={handleChange}
          aria-invalid={!!errors.phone}
        />
        <small className={`field__help ${errors.phone ? "is-error" : ""}`}>
          {errors.phone || "   ."}
        </small>
      </label>

      <label className="field">
        <span className="field__label"></span>
        <input
          type="text"
          name="address"
          placeholder="  "
          value={form.address}
          onChange={handleChange}
          aria-invalid={!!errors.address}
        />
        <small className={`field__help ${errors.address ? "is-error" : ""}`}>
          {errors.address || "     ."}
        </small>
      </label>

      <label className="field">
        <span className="field__label"></span>
        <input
          type="email"
          name="email"
          placeholder="name@example.com"
          value={form.email}
          onChange={handleChange}
          aria-invalid={!!errors.email}
        />
        <small className={`field__help ${errors.email ? "is-error" : ""}`}>
          {errors.email || "    ."}
        </small>
      </label>

      <label className="field">
        <span className="field__label"></span>
        <input
          type="password"
          name="password"
          placeholder="8  "
          value={form.password}
          onChange={handleChange}
          aria-invalid={!!errors.password}
        />
        <small className={`field__help ${errors.password ? "is-error" : ""}`}>
          {errors.password || ",   8  ."}
        </small>
      </label>

      <button type="submit" className="btn btn--primary btn--md">
         
      </button>
      {message && <p className="form-success">{message}</p>}
    </form>
  );
}

export default FormSample;

