import { useState, useRef } from "react";
import Image from "../Image/Image";
import Loading from "../Loading/Loading";
import "./FileUpload.scss";

const MAX_SIZE = 300 * 1024 * 1024; // 300MB
const MAX_FILES = 3;

const isImage = (file) => file.type.startsWith("image/");

const formatSize = (bytes) => {
  if (bytes >= 1024 * 1024 * 1024) return `${(bytes / (1024 * 1024 * 1024)).toFixed(1)} GB`;
  if (bytes >= 1024 * 1024) return `${(bytes / (1024 * 1024)).toFixed(1)} MB`;
  if (bytes >= 1024) return `${(bytes / 1024).toFixed(1)} KB`;
  return `${bytes} B`;
};

function FileUpload() {
  const [files, setFiles] = useState([]);
  const [loadingFiles, setLoadingFiles] = useState(new Set());
  const inputRef = useRef(null);

  /**
   *     
   *  input onChange  
   * @param {Event} event - input change  
   */
  const handleFileChange = (event) => {
    //    
    const selectedFiles = Array.from(event.target.files || []);
    if (selectedFiles.length === 0) return;

    //    (image/*  )
    const imageFiles = selectedFiles.filter(isImage);

    //      
    if (imageFiles.length === 0) {
      alert("    .");
      event.target.value = ""; // input 
      return;
    }

    //    (   +     MAX_FILES  )
    const remainingSlots = MAX_FILES - files.length;
    if (imageFiles.length > remainingSlots) {
      alert(` ${MAX_FILES}   . ( ${files.length},   ${remainingSlots})`);
      event.target.value = ""; // input 
      return;
    }

    //    (  MAX_SIZE(300MB)  )
    const oversizedFiles = imageFiles.filter((file) => file.size > MAX_SIZE);
    if (oversizedFiles.length > 0) {
      alert(" 300MB   .");
      event.target.value = ""; // input 
      return;
    }

    //  URL  (Blob URL    )
    const newFiles = imageFiles.map((file) => ({
      id: Date.now() + Math.random(), //  ID 
      file, //  File 
      name: file.name, // 
      size: file.size, //  
      type: file.type, // MIME 
      preview: URL.createObjectURL(file), // Blob URL  ()
    }));

    //    (     )
    const newFileIds = newFiles.map((f) => f.id);
    setLoadingFiles((prev) => {
      const newSet = new Set(prev);
      newFileIds.forEach((id) => newSet.add(id)); //   ID  Set 
      return newSet;
    });

    //     
    setFiles((prev) => [...prev, ...newFiles]);
    event.target.value = ""; // input  (    )
  };

  /**
   *     
   *     
   * @param {string|number} id -    ID
   */
  const handleRemove = (id) => {
    setFiles((prev) => {
      //   
      const fileToRemove = prev.find((f) => f.id === id);
      // Blob URL   (  )
      if (fileToRemove && fileToRemove.preview) {
        URL.revokeObjectURL(fileToRemove.preview);
      }
      //  ID    
      return prev.filter((f) => f.id !== id);
    });
    //   
    setLoadingFiles((prev) => {
      const newSet = new Set(prev);
      newSet.delete(id);
      return newSet;
    });
  };

  /**
   *     
   * Image  onLoad  
   * @param {string|number} id -     ID
   */
  const handleImageLoad = (id) => {
    //    (  )
    setLoadingFiles((prev) => {
      const newSet = new Set(prev);
      newSet.delete(id);
      return newSet;
    });
  };

  /**
   *     
   * Image  onError  
   * @param {string|number} id -     ID
   */
  const handleImageError = (id) => {
    //    (     )
    setLoadingFiles((prev) => {
      const newSet = new Set(prev);
      newSet.delete(id);
      return newSet;
    });
  };

  /**
   *     
   * " "    
   */
  const handleClearAll = () => {
    //   Blob URL  
    files.forEach((file) => {
      if (file.preview) {
        URL.revokeObjectURL(file.preview);
      }
    });
    //   
    setFiles([]);
    //   
    setLoadingFiles(new Set());
    // input 
    if (inputRef.current) {
      inputRef.current.value = "";
    }
  };

  const canAddMore = files.length < MAX_FILES;

  return (
    <div className="file-upload-demo">
      {canAddMore && (
        <div className="file-upload-demo__field">
          <label className="file-upload-demo__label" htmlFor="component-file-input">
              ({files.length}/{MAX_FILES})
          </label>
          <input
            ref={inputRef}
            id="component-file-input"
            type="file"
            accept="image/*"
            multiple
            onChange={handleFileChange}
          />
          <p className="file-upload-demo__hint">
              {MAX_FILES}  &nbsp;&nbsp;  300MB&nbsp;&nbsp;    
          </p>
        </div>
      )}

      {files.length > 0 && (
        <div className="file-upload-demo__preview">
          <div className="file-upload-demo__preview-header">
            <p className="file-upload-demo__preview-title">  ({files.length}/{MAX_FILES})</p>
            {files.length > 0 && (
              <button
                type="button"
                className="file-upload-demo__clear-all"
                onClick={handleClearAll}
                aria-label="  "
              >
                 
              </button>
            )}
          </div>
          <div className="file-upload-demo__preview-grid">
            {files.map((file) => {
              const isLoading = loadingFiles.has(file.id);
              return (
                <div key={file.id} className="file-upload-demo__preview-item">
                  <div className="file-upload-demo__preview-image-wrapper">
                    {isLoading && (
                      <div className="file-upload-demo__preview-loading">
                        <Loading size={32} thickness={3} label="" />
                      </div>
                    )}
                    <Image
                      src={file.preview}
                      alt={file.name}
                      className="file-upload-demo__preview-image"
                      onLoad={() => handleImageLoad(file.id)}
                      onError={() => handleImageError(file.id)}
                    />
                    <button
                      type="button"
                      className="file-upload-demo__preview-remove"
                      onClick={() => handleRemove(file.id)}
                      aria-label={`${file.name} `}
                    >
                      <svg width="18" height="18" viewBox="0 0 18 18" fill="none">
                        <path
                          d="M13.5 4.5L4.5 13.5M4.5 4.5L13.5 13.5"
                          stroke="currentColor"
                          strokeWidth="1.5"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        />
                      </svg>
                    </button>
                  </div>
                  <div className="file-upload-demo__preview-info">
                    <p className="file-upload-demo__preview-name" title={file.name}>
                      {file.name}
                    </p>
                    <p className="file-upload-demo__preview-size">{formatSize(file.size)}</p>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      )}

      {files.length === 0 && (
        <div className="file-upload-demo__empty">
          <p className="file-upload-demo__placeholder">  .</p>
        </div>
      )}
    </div>
  );
}

export default FileUpload;
