import React, { useRef, useEffect, useState } from "react";
import Lottie from "lottie-react";
import "./Lottie.scss";

/**
 * Lottie  
 * @param {Object} props
 * @param {Object|string} props.animationData - Lottie JSON   URL
 * @param {boolean} props.loop -    (: true)
 * @param {boolean} props.autoplay -    (: true)
 * @param {number} props.speed -   (: 1)
 * @param {string} props.className -  CSS 
 * @param {number} props.width -  (px)
 * @param {number} props.height -  (px)
 * @param {Function} props.onComplete -    
 * @param {Function} props.onLoopComplete -    
 */
const LottieAnimation = ({
  animationData,
  loop = true,
  autoplay = true,
  speed = 1,
  className = "",
  width,
  height,
  onComplete,
  onLoopComplete,
}) => {
  const lottieRef = useRef(null);

  // animationData URL  
  const [animationJson, setAnimationJson] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (typeof animationData === "string") {
      setIsLoading(true);
      setError(null);
      fetch(animationData)
        .then((res) => {
          if (!res.ok) {
            throw new Error(`HTTP error! status: ${res.status}`);
          }
          return res.json();
        })
        .then((data) => {
          setAnimationJson(data);
          setIsLoading(false);
        })
        .catch((err) => {
          console.error("Lottie   :", err);
          setError(err);
          setIsLoading(false);
        });
    } else if (animationData) {
      setAnimationJson(animationData);
    }
  }, [animationData]);

  // speed     
  useEffect(() => {
    if (lottieRef.current && lottieRef.current.setSpeed) {
      lottieRef.current.setSpeed(speed);
    }
  }, [speed]);

  if (isLoading) {
    return (
      <div className={`lottie-container ${className}`} style={{ width, height }}>
        <div className="lottie-loading"> ...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`lottie-container ${className}`} style={{ width, height }}>
        <div className="lottie-error">   .</div>
      </div>
    );
  }

  if (!animationJson) {
    return null;
  }

  return (
    <div className={`lottie-container ${className}`} style={{ width, height }}>
      <Lottie
        lottieRef={lottieRef}
        animationData={animationJson}
        loop={loop}
        autoplay={autoplay}
        speed={speed}
        onComplete={onComplete}
        onLoopComplete={onLoopComplete}
      />
    </div>
  );
};

export default LottieAnimation;

