const LottiePreview = () => {
  const [isPlaying, setIsPlaying] = useState(true);
  const [speed, setSpeed] = useState(1);
  const [loop, setLoop] = useState(true);

  //  Lottie  URL  (  )
  const sampleUrl = "https://assets5.lottiefiles.com/packages/lf20_jcikwtux.json";
  
  //   JSON   (  )
  // const loadingAnimation = {
  //   v: "5.7.4",
  //   fr: 30,
  //   ip: 0,
  //   op: 60,
  //   w: 200,
  //   h: 200,
  //   nm: "Loading Circle",
  //   ddd: 0,
  //   assets: [],
  //   layers: [...]
  // };

  return (
    <div className="guide-preview guide-preview--lottie">
      <div className="lottie-preview__container">
        <div className="lottie-preview__animation">
          <LottieAnimation
            animationData={sampleUrl}
            loop={loop}
            autoplay={isPlaying}
            speed={speed}
            width={200}
            height={200}
          />
        </div>
        
        <div className="lottie-preview__controls">
          <div className="lottie-control-group">
            <label className="lottie-control-label">
              <input
                type="checkbox"
                checked={isPlaying}
                onChange={(e) => setIsPlaying(e.target.checked)}
              />
              <span> </span>
            </label>
          </div>

          <div className="lottie-control-group">
            <label className="lottie-control-label">
              <input
                type="checkbox"
                checked={loop}
                onChange={(e) => setLoop(e.target.checked)}
              />
              <span> </span>
            </label>
          </div>

          <div className="lottie-control-group">
            <label className="lottie-control-label">
              <span> : {speed}x</span>
              <input
                type="range"
                min="0.5"
                max="2"
                step="0.1"
                value={speed}
                onChange={(e) => setSpeed(parseFloat(e.target.value))}
              />
            </label>
          </div>
        </div>

        <div className="lottie-preview__info">
          <p>Lottie  JSON        .</p>
          <p>After Effects Bodymovin   JSON  .</p>
        </div>
      </div>
    </div>
  );
};

