import { useState, useRef, useEffect } from "react";
import "./Header.scss";

function Header({ currentPage, onPageChange }) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [expandedItems, setExpandedItems] = useState({});
  //   DOM  (2, 3 )
  const submenuRefs = useRef({});
  // expandedItems     ref
  const expandedItemsRef = useRef(expandedItems);

  /**
   *    
   *       /
   */
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  /**
   * 2, 3    
   *      /
   * @param {string} key -  ID
   */
  const toggleExpanded = (key) => {
    setExpandedItems(prev => {
      const newState = {
        ...prev,
        [key]: !prev[key]
      };
      // ref   
      expandedItemsRef.current = newState;
      return newState;
    });
  };

  /**
   *      
   *    
   */
  const closeMenu = () => {
    setIsMenuOpen(false);
    setExpandedItems({});
  };

  // expandedItems   ref 
  useEffect(() => {
    expandedItemsRef.current = expandedItems;
  }, [expandedItems]);

  /**
   *      
   * transition     
   * @param {HTMLElement} ref -  
   * @returns {number} -   
   */
  const measureHeight = (ref) => {
    if (!ref) return 0;
    
    //  max-height 
    const originalMaxHeight = ref.style.maxHeight;
    
    // transition       
    ref.style.transition = "none";
    
    // max-height      
    ref.style.maxHeight = "9999px";
    
    //    
    // offsetHeight  reflow 
    // eslint-disable-next-line no-unused-expressions
    ref.offsetHeight;
    
    // scrollHeight  (  )
    const height = ref.scrollHeight;
    
    // max-height    (transition  none )
    // transition recalculateAllHeights    
    ref.style.maxHeight = originalMaxHeight;
    
    return height;
  };

  /**
   *      
   * @param {Object} expandedItemsState -  expandedItems 
   */
  const recalculateAllHeights = (expandedItemsState) => {
    //    max-height 0 
    Object.keys(submenuRefs.current).forEach((key) => {
      const ref = submenuRefs.current[key];
      if (ref && !expandedItemsState[key]) {
        ref.style.maxHeight = "0";
      }
    });

    //   3 2 
    const depth3Keys = []; // 3  (submenu.id)
    const depth2Keys = []; // 2  (menu.id)
    
    Object.keys(expandedItemsState).forEach((key) => {
      if (expandedItemsState[key]) {
        const parentId = findParentMenuId(key);
        if (parentId) {
          // 3  (  = submenu.id)
          depth3Keys.push(key);
        } else {
          // 2  (  = menu.id)
          depth2Keys.push(key);
        }
      }
    });

    // 1: 3     ()
    //       
    depth3Keys.forEach((key) => {
      const ref = submenuRefs.current[key];
      if (ref) {
        //   (transition   )
        const height = measureHeight(ref);
        
        // transition    
        //   0    height  transition
        ref.style.transition = "";
        ref.style.maxHeight = height + "px";
      }
    });

    // 2: 2    (3   )
    // 3    2 scrollHeight 
    depth2Keys.forEach((key) => {
      const ref = submenuRefs.current[key];
      if (ref) {
        //   (transition   )
        const height = measureHeight(ref);
        
        // transition    
        //   0    height  transition
        ref.style.transition = "";
        ref.style.maxHeight = height + "px";
      }
    });
  };

  /**
   *  max-height  ( )
   * expandedItems   
   * 3   2   
   */
  useEffect(() => {
    let rafId1 = null;
    let rafId2 = null;
    let timeoutId = null;
    let isCancelled = false;

    // requestAnimationFrame  DOM    
    //  requestAnimationFrame      
    const updateHeights = () => {
      //   
      if (isCancelled) return;

      // ref  expandedItems    
      const currentExpandedItems = expandedItemsRef.current;

      //   : DOM    
      recalculateAllHeights(currentExpandedItems);

      //   :        (3  2 )
      rafId2 = requestAnimationFrame(() => {
        if (isCancelled) return;
        
        const latestExpandedItems = expandedItemsRef.current;
        recalculateAllHeights(latestExpandedItems);
        
        //   : CSS transition     
        // transition 0.3s 350ms  
        timeoutId = setTimeout(() => {
          if (isCancelled) return;
          
          const finalExpandedItems = expandedItemsRef.current;
          recalculateAllHeights(finalExpandedItems);
        }, 350);
      });
    };

    // DOM    
    rafId1 = requestAnimationFrame(() => {
      requestAnimationFrame(updateHeights);
    });

    // cleanup :          
    return () => {
      isCancelled = true;
      if (rafId1 !== null) {
        cancelAnimationFrame(rafId1);
      }
      if (rafId2 !== null) {
        cancelAnimationFrame(rafId2);
      }
      if (timeoutId !== null) {
        clearTimeout(timeoutId);
      }
    };
  }, [expandedItems]);

  // GNB   (3 )
  const gnbMenu = [
    {
      id: "menu1",
      label: " 1",
      children: [
        {
          id: "submenu1-1",
          label: " 1-1",
          children: [
            { id: "depth3-1-1", label: "3 1-1-1", href: "#" },
            { id: "depth3-1-2", label: "3 1-1-2", href: "#" }
          ]
        },
        {
          id: "submenu1-2",
          label: " 1-2",
          children: [
            { id: "depth3-1-3", label: "3 1-2-1", href: "#" },
            { id: "depth3-1-4", label: "3 1-2-2", href: "#" }
          ]
        }
      ]
    },
    {
      id: "menu2",
      label: " 2",
      children: [
        {
          id: "submenu2-1",
          label: " 2-1",
          children: [
            { id: "depth3-2-1", label: "3 2-1-1", href: "#" },
            { id: "depth3-2-2", label: "3 2-1-2", href: "#" }
          ]
        },
        {
          id: "submenu2-2",
          label: " 2-2",
          children: [
            { id: "depth3-2-3", label: "3 2-2-1", href: "#" }
          ]
        }
      ]
    },
    {
      id: "menu3",
      label: " 3",
      children: [
        {
          id: "submenu3-1",
          label: " 3-1",
          href: "#"
        },
        {
          id: "submenu3-2",
          label: " 3-2",
          href: "#"
        }
      ]
    }
  ];

  /**
   *   ID  (3  2 ID )
   * @param {string} childId -   ID
   * @returns {string|null} -   ID  null
   */
  const findParentMenuId = (childId) => {
    for (const menu of gnbMenu) {
      if (menu.children) {
        for (const submenu of menu.children) {
          if (submenu.id === childId) {
            // 3  2 ID 
            return menu.id;
          }
        }
      }
    }
    return null;
  };

  return (
    <header className="header">
      <div className="header__inner">
        {/*  */}
        <div className="header__logo">
          <h1></h1>
        </div>

        {/*   */}
        <button
          className={`header__hamburger ${isMenuOpen ? "is-active" : ""}`}
          onClick={toggleMenu}
          aria-label=" "
          aria-expanded={isMenuOpen}
        >
          <span className="header__hamburger-line"></span>
          <span className="header__hamburger-line"></span>
          <span className="header__hamburger-line"></span>
        </button>
      </div>

      {/*    */}
      <aside className={`header__aside ${isMenuOpen ? "is-open" : ""}`}>
        <div className="header__aside-inner">
          {/*   */}
          <button
            className="header__aside-close"
            onClick={closeMenu}
            aria-label=" "
          >
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" aria-hidden="true">
              <path d="M5 5L15 15M15 5L5 15" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          </button>

          {/*   */}
          <nav className="header__nav">
            <ul className="header__nav-list">
              {/* GNB  (3) */}
              {gnbMenu.map((menu) => (
                <li key={menu.id} className="header__nav-item header__nav-item--has-children">
                  <button
                    className="header__nav-link header__nav-link--expandable"
                    onClick={() => toggleExpanded(menu.id)}
                    aria-expanded={expandedItems[menu.id]}
                  >
                    {menu.label}
                    <span className="header__nav-arrow" aria-hidden="true">
                      <svg viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M4.5 3L7.5 6L4.5 9" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
                      </svg>
                    </span>
                  </button>

                  {/* 2  ( ) */}
                  {menu.children && (
                    <ul
                      ref={(el) => (submenuRefs.current[menu.id] = el)}
                      className={`header__nav-sublist ${expandedItems[menu.id] ? "is-open" : ""}`}
                    >
                      {menu.children.map((submenu) => (
                        <li key={submenu.id} className="header__nav-subitem">
                          {submenu.children ? (
                            <>
                              <button
                                className="header__nav-sublink header__nav-sublink--expandable"
                                onClick={() => toggleExpanded(submenu.id)}
                                aria-expanded={expandedItems[submenu.id]}
                              >
                                {submenu.label}
                                <span className="header__nav-arrow" aria-hidden="true">
                                  <svg viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M4.5 3L7.5 6L4.5 9" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
                                  </svg>
                                </span>
                              </button>

                              {/* 3  ( ) */}
                              {submenu.children && (
                                <ul
                                  ref={(el) => (submenuRefs.current[submenu.id] = el)}
                                  className={`header__nav-sublist header__nav-sublist--depth3 ${expandedItems[submenu.id] ? "is-open" : ""}`}
                                >
                                  {submenu.children.map((depth3) => (
                                    <li key={depth3.id} className="header__nav-subitem">
                                      <a
                                        href={depth3.href}
                                        className="header__nav-sublink"
                                        onClick={closeMenu}
                                      >
                                        {depth3.label}
                                      </a>
                                    </li>
                                  ))}
                                </ul>
                              )}
                            </>
                          ) : (
                            <a
                              href={submenu.href}
                              className="header__nav-sublink"
                              onClick={closeMenu}
                            >
                              {submenu.label}
                            </a>
                          )}
                        </li>
                      ))}
                    </ul>
                  )}
                </li>
              ))}
            </ul>
          </nav>
        </div>
      </aside>

      {/*  */}
      {isMenuOpen && (
        <div
          className="header__overlay"
          onClick={closeMenu}
          aria-hidden="true"
        ></div>
      )}
    </header>
  );
}

export default Header;
